export default {
	data() {
		return {
			couponVisible: 0
		}
	},
	props: {
		value: {
			type: String | Number,
			default: 0
		},
		list: {
			type: Array
		}
	},
	watch: {
		value(val) {
			this.couponVisible = val
		},
		couponVisible(val) {
			this.$emit('input', val)
		}
	},
	mounted() {
		this.couponVisible = this.value
	},
	methods: {
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.couponVisible = 2;
			setTimeout(()=>{
				this.couponVisible = state;
			}, timer)
		},
		useCoupon(item, index) {
			this.$emit('change', item.coupon, index)
		}
	}
}
